import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedBasisModule } from '../../../../../../../app/sharedBasis.module'
import { Message, ConfirmationService } from 'primeng/primeng';
import { CommonHttpService } from '../../../http/common.http.service';
import { PowerManageBean } from '../bean/power-manage.bean'
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-power-copy',
  templateUrl: './power-copy.component.html',
  styleUrls: ['./power-copy.component.scss']
})
export class PowerCopyComponent implements OnInit {

  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //提示信息
  msgs: Message[] = [];
  //实体
  powerManageBean: PowerManageBean = new PowerManageBean();
  userform: FormGroup; //校验
  copyPermissionsId: string//被复制的权限编号
  isCopyFlag: string = 'N'//是否复制权限关系
  constructor(
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
    public fb: FormBuilder,
  ) { }

  ngOnInit() {
    this.userform = this.fb.group({
      'permissionsId': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
      'permissionsDescription': new FormControl('', [Validators.pattern(/^[\u4E00-\u9FA5\(\)\（\）]{1,60}$/), Validators.required]),
    });
    this.powerManageBean = this.inValue;
    this.copyPermissionsId = this.powerManageBean.permissionsId
  }
  //勾选复制权限事件
  onChanges(event) {
    if (event) {
      this.isCopyFlag = 'Y';
    } else {
      this.isCopyFlag = 'N';
    }
  }
  copyClick() {
    //表单校验
    for (let i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (let i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    let params = {
      copyPermissionsId: this.copyPermissionsId,
      isCopyFlag: this.isCopyFlag,
      permissionsId: this.powerManageBean.permissionsId,
      permissionsDescription: this.powerManageBean.permissionsDescription,
      permissionType: "FEATURES"
    }
    this.commonHttpService.copyPermissions(params).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: "复制成功" });
        this.outValue.emit({
          display:false,
          msg:this.msgs
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }
  closeClick() {
    this.outValue.emit(false)
  }
}
