import { Component, OnInit, Input, Output, EventEmitter, ViewChild, ComponentRef } from '@angular/core';
import { PowerManageBean } from './bean/power-manage.bean';
import { CommonHttpService } from '../../http/common.http.service';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";


@Component({
    selector: 'power-manage',
    templateUrl: 'power-manage.html',
    styleUrls: ['power-manage.scss'],
    providers: [ConfirmationService, Commfunc]
})

export class PowerManage implements OnInit {
    @ViewChild("powerList") powesrList: ComponentRef<any>;
    // @Input() inValue: any;

    // @Output() outValue: EventEmitter<any> = new EventEmitter();
    // 表格中显示的信息
    tabMesg: any = tableMessage;
    userform: FormGroup; //校验
    //页面头部标题
    precredit: string = '权限查询';
    // 表体内容
    tableData: any;
    powerManageBean: PowerManageBean = new PowerManageBean()

    //模态框的标题
    headerTitle: string;
    //模态框是否显示
    display: boolean = false;
    showModel: string;

    //总页数
    total: string;
    //每页多少条
    pageSize: number = 10;

    //提示信息
    msgs: Message[] = [];
    //传出的值
    //  inValue: string;
    //修改的参数
    updateValue: any = [];
    //新增的参数
    addValue: any = [];
    //复制的参数
    copyValue: any = [];
    //当前页数
    currentPage: number;
    //每页显示条数
    rows: number
    first: number = 0;
    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public fb: FormBuilder,
        public commfunc: Commfunc
    ) { }

    ngOnInit() {
        this.powerManageBean.pageSize = 10;
        this.powerManageBean.pageNum = 1;
        this.queryClick();

        //按回车键查询
        let _thiss = this;
        document.onkeydown = function (event) {
            if (event && event.keyCode === 13) {
                _thiss.query();
            }
        }
    }

    //查询
    queryClick() {
        this.commonHttpService.querySecurityPermissionList(this.powerManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.permissionList;
                this.total = data.total;
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    query() {
        this.first = 0;
        this.powerManageBean.pageNum = 1;
        this.commonHttpService.querySecurityPermissionList(this.powerManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.permissionList;
                this.total = data.total;
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    // 修改
    update(item) {
        this.headerTitle = '权限描述修改';
        this.display = true;
        this.showModel = '2';
        this.updateValue = item;
    }

    //修改的回调
    updateCall(param) {
        this.display = param;
        // this.powerManageBean.pageNum = 1;
        // this.powerManageBean = new PowerManageBean();
        // this.powerManageBean.pageNum = this.currentPage;
        // this.powerManageBean.pageSize = this.rows;
        this.queryClick();
    }

    //删除事件
    delete(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                // 条件
                let param = { permissionsId: item.permissionsId };
                this.commonHttpService.deletePermissions(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.meaasge });
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        });
    }
    // 增加按钮事件
    add() {
        this.headerTitle = '权限添加';
        this.display = true;
        this.showModel = '1'
    }

    //添加的回调
    addCall(param) {
        this.display = param;
        // this.powerManageBean.pageNum = 1;
        this.queryClick();
    }


    // 分配资源
    powerList(item) {
        this.headerTitle = '功能列表';
        this.display = true;
        this.showModel = '3';
        this.updateValue = item;
        setTimeout(() => {
            this.updateValue = null;
        }
            , 200)
    }
    // 分配资源回调
    powerListCall(param) {
        this.display = param;
        this.queryClick();
    }
    copy(item) {
        this.display = true;
        this.showModel = '4';
        this.headerTitle = '权限关系复制';
        this.copyValue = item;
    }
    copyCall(param) {
        this.display = param.display;
        this.msgs = param.msg;
        this.queryClick();
    }
    //重置按钮事件
    resetClick() {
        this.powerManageBean.permissionsId = "";
        this.powerManageBean.permissionsDescription = "";
    }

    //分页
    paginate(event) {
        this.rows = event.rows * 1;
        //每页显示的条数
        this.powerManageBean.pageSize = this.rows;
        //当前页
        this.currentPage = event.page + 1;
        this.powerManageBean.pageNum = this.currentPage;
        this.first = event.rows * event.page
        //调用查询的方法
        this.queryClick();
        // this.powerManageBean.pageNum = 1;
    }
    //返回的值
    outValue(param) {
        this.display = param.display;
    }

    hide() {
        // this.powesrList.destroy();
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}