import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from '../../../../../../../app/sharedBasis.module';
import { Message, ConfirmationService } from 'primeng/primeng';
import { CommonHttpService } from '../../../http/common.http.service';
import { PowerManageBean } from '../bean/power-manage.bean'

@Component({
  selector: 'power-add-manage',
  templateUrl: 'power-add-manage.html',
  styleUrls:['power-add-manage.scss'],
  providers: [CommonHttpService, ConfirmationService]
})

export class PowerAddManage implements OnInit {

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  userform: FormGroup; //校验

  //提示信息
  msgs: Message[] = [];

  powerManageBean: PowerManageBean = new PowerManageBean();
  constructor(
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
    public fb: FormBuilder,
  ) {
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'permissionsId': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
      'permissionsDescription': new FormControl('', [Validators.pattern(/^([\u4E00-\u9FA5\(\)\（\）]){1,60}$/), Validators.required]),
    });
  }
  // 确定
  preserveClick() {
    this.powerManageBean.permissionType='FEATURES';
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    //提交服务
    // ;
    this.commonHttpService.createPermissions(this.powerManageBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })

  }

  //取消
  modifyCloseBtn() {
    this.outValue.emit(false);
    
  }

}