  //选中树的时候
  onChange(event) {
    if (this.selectedList.length > 0) {
      //判断当前的是否被选中
      if (this.selectedList.indexOf(event.data) != -1) {
        //选中所有子的
        if (event.children) {
          this.selectCheck(event.children, this);
        }
        //选中某个子的去掉上级的勾勾
        if (event.parent) {
          //选中上级
          this.selectParent(event.parent, this);
        }
      } else {
        //去掉选中
        if (event.children) {
          this.unSelectCheck(event.children);
        }
        //去掉某个子的去掉上级的勾勾
        if (event.parent) {
          this.forSelectParent(event.parent, this);
        }
      }
    }
  }


  //循环去掉上级的选中值
  forSelectParent(list, thiss) {
    if (thiss.selectParentList(list.children, thiss)) {
      if (thiss.selectedList.indexOf(list.data) == -1) {
        thiss.selectedList.push(list.data);
      }
    } else {
      if (thiss.selectedList.indexOf(list.data) != -1) {
        //判断子是有勾选的
        if (!thiss.selectParentList(list.children, thiss)) {
          let flag=true;//判断子是否为菜单
          list.children.forEach(el=>{
            if(el.sourceTypeId!='FEATURES_MENU'){
              flag=false;
            }
          })
          if(flag){
            thiss.selectedList.splice(thiss.selectedList.indexOf(list.data), 1);
          }
        }
      }
    }
    //再次判断是否还有上级
    if (list.parent) {
      // list.children.forEach(element => {
        this.forSelectParent(list.parent, thiss);
      // });

    }
  }

  //判断所有的子是否选中如果选中父也选中
  selectParent(list, thiss) {
    if (list) {
      if (thiss.selectedList.indexOf(list.data) == -1) {
        thiss.selectedList.push(list.data);
        if (list.parent) {
          this.selectParent(list.parent, thiss);
        }
      } else {
        if (list.parent) {
          this.selectParent(list.parent, thiss);
        }
      }
    }

  }

  //判断子是否全去掉,是就去掉父
  selectParentList(list, thiss) {
    let status = false;
    if (list) {
      for (let i = 0; i < list.length; i++) {
        let element = list[i];
        if (thiss.selectedList.length > 0) {
          if (thiss.selectedList.indexOf(element.data) != -1) {
            status = true;
          }
        } else {
          status = false;
          return;
        }
      }
      return status;
    }
  }
  //选中所有的子
  selectCheck(list, thiss) {
    list.forEach(element => {
      if (thiss.selectedList.indexOf(element.data) == -1) {
        thiss.selectedList.push(element.data);
      }
      if (element.children && element.children.length > 0) {
        this.selectCheck(element.children, thiss);
      }
    });
  }

  //去掉全选
  unSelectCheck(list) {
    list.forEach(element => {
      if (this.selectedList.length == 1) {
        if (this.selectedList.indexOf(element.data) != -1) {
          this.selectedList = []
        }
      } else {
        if(this.selectedList.indexOf(element.data)!=-1){
          this.selectedList.splice(this.selectedList.indexOf(element.data), 1);
        }
        if (element.children && element.children.length > 0) {
          this.unSelectCheck(element.children);
        }
      }
    });
  }

  //取消按钮
  cancelClick() {
    this.selectedList = null
    this.outValue.emit(false);
  }
